import React from 'react';
import { useDispatch } from "react-redux"
import Swal from 'sweetalert2';
import { startLogin, startRegister } from '../../actions/authActions';
import { useForm } from '../../hooks/useForm';


export const LoginScreen = () => {
    const dispatch = useDispatch()

    const [ valoresLogin, cambioInputLogin] = useForm({
        correoL: '',
        passwordL: ''
    })
    const {correoL, passwordL} = valoresLogin

    const [ valoresRegister, cambioInputRegister] = useForm({
        nombreR: '',
        correoR: '',
        passwordR: '',
        password2R: ''
    })
    const {nombreR, correoR, passwordR, password2R} = valoresRegister

    const envioFormLogin = (e) => {
        e.preventDefault();
        dispatch(startLogin(correoL, passwordL))
    }

    const envioFormRegister = (e) => {
        e.preventDefault();
        if(validar()){
            dispatch(startRegister(nombreR, correoR, passwordR))
        }
        
    }

    const validar = () => {
        if(passwordR !== password2R){
            Swal.fire({
                title: 'Las contraseñas son diferentes',
                icon: 'error'
            })
            return false;
        }
        return true;
    }
    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form onSubmit={envioFormLogin}>
                        <div className="form-group">
                            <input 
                                type="text"
                                className="form-control my-4"
                                placeholder="Correo"
                                name="correoL"
                                value={correoL}
                                onChange={cambioInputLogin}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control my-4"
                                placeholder="Contraseña"
                                name="passwordL"
                                value={passwordL}
                                onChange={cambioInputLogin}
                            />
                        </div>
                        <div className="form-group">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form onSubmit={envioFormRegister}>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control my-4"
                                placeholder="Nombre"
                                name="nombreR"
                                value={nombreR}
                                onChange={cambioInputRegister}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name="correoR"
                                value={correoR}
                                onChange={cambioInputRegister}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control my-4"
                                placeholder="Contraseña" 
                                name="passwordR"
                                value={passwordR}
                                onChange={cambioInputRegister}
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control my-4"
                                placeholder="Repita la contraseña" 
                                name="password2R"
                                value={password2R}
                                onChange={cambioInputRegister}
                            />
                        </div>

                        <div className="form-group">
                            <input 
                                type="submit" 
                                className="btnSubmit" 
                                value="Crear cuenta" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}