import React from 'react'
import { useDispatch } from 'react-redux'
import { abrirModal } from '../../actions/uiActio'

export const BotonAgregar = () => {
    const dispatch = useDispatch()
    const botonAgregar = () => {
        dispatch(abrirModal())

    }
    return (
       <button className="agregar" onClick={botonAgregar}><i className='bx bx-plus-medical' ></i></button>
    )
}
