import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";
import { finishChecking } from "./authActions";




export const iniciarGrabacion = (evento) => {
    return async(dispatch, getState) => {
        const {uid, nombre} = getState().auth;
        const respuesta = await fetchConToken('eventos', evento, 'POST');
        const body = await respuesta.json()
        if(body.ok){
            evento.id = body.evento.id;
            evento.usuario =  {
                uid,
                nombre
            }
            dispatch(agregarEvento(evento))
        }else{
            dispatch(finishChecking())
        }
    }

}

export const agregarEvento = (evento) => ({
    type: types.agregarEvento,
    payload: evento
})

export const iniciarEliminacion = (id) => {
    return async(dispatch) => {
        try {
            const respuesta = await fetchConToken(`eventos/${id}`, {}, 'DELETE')
            const body = await respuesta.json()
            if(body.ok){
                dispatch(borrarEvento(id))
            }else {
                Swal.fire({
                    title: body.msg, icon: 'error'
                })
            }
        } catch (error) {
            console.log(error)
        }
    }
}
 const borrarEvento = (id) => ({
    type: types.borrarEvento,
    payload: id
})

export const activarEvento = (evento) =>({
    type: types.activarEvento,
    payload: evento
})

export const limpiarEvento = () =>({
    type: types.limpiarEvento,
})

export const inicarEdicion = (evento) => {
    return async(dispatch, getState) => {
        const {id} = getState().evento.eventoActivo;
        try {
            const respuesta = await fetchConToken(`eventos/${id}`, evento, 'PUT')
            const body = await respuesta.json()
            console.log(body)
            if(body.ok){
                dispatch(editarEvento(body.eventoNuevo))
            }else{
                Swal.fire({
                    title: body.msg, icon: 'error'
                })
            }
        } catch (error) {
            console.log(error)
            
        }

    } 
}

const editarEvento = (evento) =>({
    type: types.editarEvento,
    payload: evento
})



export const eventStartLoading = () => {
    return async(dispatch) => {
        try {
            const respuesta = await fetchConToken('eventos')
            const body = await respuesta.json()
            if(body.ok){
                const eventos = body.eventos;
                dispatch(eventsLoaded(eventos))
            }
        } catch (error) {
            
        }

    }
}

const eventsLoaded = (eventos) => ({
    type: types.cargaEventos,
    payload: eventos
})
