import React, { useEffect, useState } from 'react'
import Modal from 'react-modal'
import DateTimePicker from 'react-datetime-picker'
import moment from 'moment';
import { useForm } from '../../hooks/useForm';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { cerrarrModal } from '../../actions/uiActio';
import { inicarEdicion, iniciarGrabacion, limpiarEvento } from '../../actions/eventosActions';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};


Modal.setAppElement('#root');
const fechaInicio = moment().minutes(0).seconds(0).add(1, 'hours');
const fechaFinal = fechaInicio.clone().add(1, 'hours')



export const CalendarModal = () => {
    const {abierto} = useSelector(state => state.ui)
    const {eventoActivo} = useSelector(state => state.evento)
    const dispatch = useDispatch()


    const [valores, cambioInput, setValores, reset] = useForm({
        start: fechaInicio.toDate(),
        end: fechaFinal.toDate(),
        title: '',
        nota: ''
    })
    const {title, nota, start, end} = valores;

    const cambioFechaInicio = (e) => {
        setdiaInicio(e)
        setValores({
            ...valores,
            start: e
        })
    }

    const cambioFechaFin= (e) => {
        setdiaFin(e)
        setValores({
            ...valores,
            end: e
        })
    }

    const closeModal = () => {
        console.log("Cerrando")
        dispatch(cerrarrModal())
        dispatch(limpiarEvento())
    }

    const [diaInicio, setdiaInicio] = useState(fechaInicio.toDate());
    const [diaFin, setdiaFin] = useState(fechaFinal.toDate());


    useEffect(() => {
        if(eventoActivo){
            setValores(eventoActivo)
        }else{
            setValores({
                start: fechaInicio.toDate(),
                end: fechaFinal.toDate(),
                title: '',
                nota: ''
            })
        }
    }, [setValores, eventoActivo])


    const envioForm = (e) => {
        e.preventDefault();
        const momentStart = moment(start)
		const momentEnd = moment(end)
		
		if(momentStart.isSameOrAfter(momentEnd)){
            return Swal.fire({
                title: 'La fecha 2 debe ser mayor',
                icon: 'error'
            })
        }

        if(title.trim().length < 2){
            return Swal.fire({
                title: 'Ingresa el titulo',
                icon: 'error'
            })
        }
        if(eventoActivo){
            dispatch(inicarEdicion(valores))
        }else{
            dispatch(iniciarGrabacion(valores))
        }
        

        closeModal()
        dispatch(limpiarEvento())
        reset()
    }
   
    return (

        <Modal
            isOpen={abierto}					//Define si esta abierto o no con(true/false)
            onRequestClose={closeModal}			//Se activa cuando damos click afuera del modal
            closeTimeoutMS={200}				//MS en que tarda en cerrar, especie de delay
            style={customStyles}				//Son los que copiamos arriba,viene con el modal
            className="modal"
            overlayClassName="modal-fondo"
        >
            <h1> Nuevo evento </h1>
            <hr />
            <form className="container" onSubmit={envioForm}>

                <div className="form-group">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker
                        onChange={cambioFechaInicio}
                        value={diaInicio}
                        className="form-control"
                    />
                </div>

                <div className="form-group">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker
                        onChange={cambioFechaFin}
                        value={diaFin}
                        minDate={diaInicio}				//Minimo el dia Inicio
                        className="form-control"
                    />
                </div>

                <hr />
                <div className="form-group">
                    <label>Titulo y notas</label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Título del evento"
                        name="title"
                        value={title}
                        onChange={cambioInput}
                        autoComplete="off"
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
                </div>

                <div className="form-group">
                    <textarea
                        type="text"
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="nota"
                        value={nota}
                        onChange={cambioInput}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </Modal>

    )
}
