import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { iniciarEliminacion } from '../../actions/eventosActions'

export const BotonBorrar = () => {
    const {eventoActivo} = useSelector(state => state.evento)
    const dispatch = useDispatch()
    const botonBorrar = () => {
        dispatch(iniciarEliminacion(eventoActivo.id))

    }
    return (
        <button className="borrar" onClick={botonBorrar}><i className='bx bxs-trash-alt'></i></button>
    )
}
