import React, { useEffect, useState } from 'react'
import { Navbar } from '../ui/Navbar'
import { Calendar, momentLocalizer } from 'react-big-calendar'
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'moment/locale/es'
import moment from 'moment'
import { CalendarEvent } from '../calendar/CalendarEvent'
import { messages } from '../../helpers/messages';
import { CalendarModal } from './CalendarModal';
import { useDispatch, useSelector } from 'react-redux';
import { abrirModal } from '../../actions/uiActio';
import { activarEvento, eventStartLoading } from '../../actions/eventosActions';
import { BotonAgregar } from '../ui/BotonAgregar';
import { BotonBorrar } from '../ui/BotonBorrar';


const localizer = momentLocalizer(moment)
moment.locale('es');

const eventStyleGetter = (event, start, end, select) => {
    const style = {
        backgroundColor: 'red',
        borderRadius: '0px',
        opacity: 0.8,
        display: 'block',
        color: '#fff'
    }
    return {
        style
    }
}

// const events = [{
//     title: 'Cumpleaños del Jefe',
//     start: moment().toDate(),
//     end: moment().add(2, 'hours').toDate(),
//     user: { name: 'Oscar' }
// }]
export const CalendarScreen = () => {
    const dispatch = useDispatch()
    const {eventos, eventoActivo} = useSelector(state => state.evento)
    useEffect(() => {
        dispatch(eventStartLoading())
    }, [dispatch])

    const onDoubleClick = (e) => {
        dispatch(abrirModal())
    }

    const onSelect = (e) => {
        dispatch(activarEvento(e))
    }

    const [vistaActual, setVistaActual] = useState(localStorage.getItem('VistaCalendar') || 'month')


    const vistaNvegador = (e) => {
        setVistaActual(e)
        localStorage.setItem("VistaCalendar", e)
    }
    return (
        <div>
            <Navbar />
            <div className="calendar-screen">
                <Calendar
                    localizer={localizer}
                    events={eventos}
                    startAccessor="start"
                    endAccessor="end"
                    messages={messages}
                    onDoubleClickEvent={onDoubleClick}
                    onSelectEvent={onSelect}
                    onView={vistaNvegador}			//Cambiamos la vista que tentremos
                    view={vistaActual}				//Mostramos la vista actual
                    eventPropGetter={eventStyleGetter}
                    components={{ event: CalendarEvent }}
                />
                <CalendarModal />
                <BotonAgregar />
                {
                    eventoActivo && <BotonBorrar />
                }
            </div>
        </div>
    )
}
