import { types } from "../types/types";
const valoresIniciales = {
    eventos: [],
    eventoActivo: null
}
export const eventoReducer = (state = valoresIniciales, action) => {
    switch (action.type) {
        case types.agregarEvento:
            return {
                ...state,
                eventos: [...state.eventos, action.payload],
                eventoActivo: null
            }
        case types.borrarEvento:
            return {
                ...state,
                eventos: state.eventos.filter(evento => evento.id !== action.payload),
                eventoActivo: null
            }
        case types.editarEvento:
            return {
                ...state,
                eventos:state.eventos.map(evento => evento.id === action.payload.id ? action.payload : evento)
            }
        case types.limpiarEvento:
            return {
                ...state,
                eventoActivo: null
            }
        case types.activarEvento:
            return {
                ...state,
                eventoActivo: action.payload
            }
        case types.cargaEventos:
            return {
                ...state,
                eventos: [...action.payload]
            }
        default:
            return state
    }
}