import Swal from "sweetalert2"
import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import { types } from "../types/types"
import { limpiarEvento } from "./eventosActions"


export const startLogin = (correo, password) => {
    return async (dispatch) => {
        const respuesta = await fetchSinToken('auth', { correo, password }, 'POST')
        const body = await respuesta.json()
        if (body.ok) {
            localStorage.setItem('token', body.token);
            localStorage.setItem('fecha-token', new Date().getTime());
            dispatch(login(body.usuario))
        } else {
            Swal.fire({
                title: 'Email/Password Incorrecto',
                icon: 'error'
            })
        }
    }
}

export const startRegister = (nombre, correo, password) => {
    return async (dispatch) => {
        const respuesta = await fetchSinToken('auth/new', { nombre, correo, password }, 'POST')
        const body = await respuesta.json()
        if (body.ok) {
            localStorage.setItem('token', body.token);
            localStorage.setItem('fecha-token', new Date().getTime());
            dispatch(login(body.usuario))
        } else {
            Swal.fire({
                title: 'Email/Password Incorrecto',
                icon: 'error'
            })
        }
    }
}

export const startChecking = () => {
    return async (dispatch) => {
        const respuesta = await fetchConToken('auth/renew')
        const body = await respuesta.json()
        if (body.ok) {
            localStorage.setItem('token', body.token);
            localStorage.setItem('fecha-token', new Date().getTime());
            dispatch(login({
                uid: body.uid,
                nombre: body.nombre
            }))
        } else {
            dispatch(finishChecking())
        }
    }
}
export const finishChecking = () => ({
    type: types.authCheckingFinish
})
const login = (usuario) => ({
    type: types.authLogin,
    payload: usuario
})

export const startLogout = () => {
    return (dispatch) => {
        localStorage.clear()
        dispatch(logout())
        dispatch(limpiarEvento())
    }
}
const logout = () => ({
    type: types.authLogout
})