import { types } from "../types/types"

export const uiReducer = (state = { abierto: false }, action) => {
    switch (action.type) {
        case types.abrirModal:
            return {
                ...state,
                abierto: true
            }
        case types.cerrarModal:
            return {
                ...state,
                abierto: false
            }
        default:
            return state
    }
}